.chart {
    color: #333;
    border-radius: 4px;
    background: -webkit-radial-gradient(#ffffff, #fafafa);
    background: -o-radial-gradient(#ffffff, #fafafa);
    background: radial-gradient(#ffffff, #fafafa);
    -webkit-box-shadow: #aaa 0px 1px 2px;
            box-shadow: #aaa 0px 1px 2px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 1%; /* width = 82% */
    margin-top:0.6em;
    padding-bottom: 2%;
}

.chart-head {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
    margin-bottom: 0.2em;
}

.chart-title {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: start;
        align-items: flex-start;
}

.chart-title h1 {
    font-size: 1.5em;
    margin-bottom: 5px;
}

.chart-data-age {
    font-size: 0.75em;
    border: 1px solid white;
    border-radius: 1em;
    padding: 2px 4px;
    background: #84677d;
    color: #fff;
}

.chart-data-age-value {
    padding: 0 5px;
}

.chart-latest-info {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: end;
        align-items: flex-end;
}

.chart-latest-data {
    font-size: 1.3em;
    margin-bottom: 5px;
}

.chart-latest-diff {
    font-size: 0.75em;
}

.chart-latest-data-diff {
    color: #63D6C5;
}

.chart-latest-time {
    background: #E9CC5C;
    border-radius: 1em;
    padding: 2px 4px;
    margin-left: 4px;
    color: #FFFFFF; 
}

.chart-body {
    margin: 1em 0;
}

.chart-body svg {
    overflow: visible;
}


